body{
    color: #fff
}
.bg{
    background-image: url('../Assets/bgMovie.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.bg-seats{
    background-color: #5f575777;
}

.bookingMovie{
    font-family: 'fontGame';
}


.ghe{
    border: 3px solid transparent;
    background-color: transparent;
    width: 20px;
    height: 20px;
    font-size: 20px;
    margin-left: 1%;
    margin-bottom: 0;
    border-radius: 5px;
    font-weight: bold;
}

.rowNumber{
    /* font-size: 25px; */
    width: 45px;
    height: 35px;
    background: none;
    border:none;
    margin-left: 2%;
    margin-bottom: 1%;
    font-size: 30px;
    color: yellow;
    
}
.firstChar{
    color: yellow;
    font-size: 35px;
    margin-right: 15px;

}

.screen{
    border-bottom: 50px solid rgb(255, 159, 95);
    border-left: 50px solid transparent;
    border-right: 50px solid transparent;
    height: 100;
    width:80%;
    filter: drop-shadow(4px 30px 20px rgba(255, 255, 255, 0.5));
    font-size: 25px;
    color: #fff;
}

.gheTrong {
    background: rgb(211, 218, 211);
    border: 3px solid rgb(211, 218, 211);
    color: #000;

    cursor: pointer;
    border-radius: 5px;
    width: 20px;
    height: 20px;

}

.gheDuocChon {
    border: 3px solid orange;
    background: orange;
    color: #000;

    cursor: no-drop !important;
    border-radius: 5px;
    width: 20px;
    height: 20px;

}

.gheDangChon {
    background: rgb(109, 238, 109);
    border: 3px solid rgb(109, 238, 109);
    color: #000;

    cursor: pointer;
    border-radius: 5px;
    width: 20px;
    height: 20px;

}
